import React from 'react';
import { render } from 'react-dom';
import Chart from './Chart';
import { TypeChooser } from "react-stockcharts/lib/helper";
import { csvParse } from "d3-dsv";
import { timeParse } from "d3-time-format";

function parseData(parse) {
	return function (d) {
		d.open = +d.Open;
		d.high = +d.High;
		d.close = +d.Close;
		d.low = +d.Low;
		d.volume = +d.Volume;
		d.date = parse(d.Date);
		return d;
	};
}

const parseDate = timeParse("%d-%m-%Y");

function getData(stockhistoryData) {
	let myHeaders = new Headers();
	myHeaders.append('Content-Type', 'text/plain; charset=utf-8');

	const requestInit = {
		//headers: myHeaders,
		mode: 'no-cors'
	};

	const promiseMSFT = fetch(stockhistoryData)
		.then(response => response.text())
		.then(data => csvParse(data, parseData(parseDate)))
	return promiseMSFT;
}

class UDFinancialChart extends React.Component {
	componentDidMount() {
		const stockhistoryData = this.props.stockData;
		getData(stockhistoryData).then(data => {
			this.setState({ data })
		})
	}
	render() {		
		if (this.state == null) {
			return <div>..loading chart data...</div>
		} else {
			<UDFinancialChart /> ,
				document.getElementById("root")
		}
		return (
			<TypeChooser>
				{type => <Chart type={type} data={this.state.data} />}
			</TypeChooser>
		)
	}
}

export default UDFinancialChart